import React, { useState } from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import './AddPlant.css';

const AddPlant = ({ plants, onBack, onAddPlant }) => {
  const [selectedPlant, setSelectedPlant] = useState(null);
  const [selectedPlantIndex, setSelectedPlantIndex] = useState(0);
  const [plantName, setPlantName] = useState('');
  const [soilSensor, setSoilSensor] = useState(false);
  const [autoWatering, setAutomatedWatering] = useState(false);
  const [moistureValue, setMoistureValue] = useState('');
  const [wateringInterval, setDaysBetweenWatering] = useState('');

  const handleAddPlant = () => {
    // prevent adding a plant without a name
    if (plantName === '') {
        return;
    }
    // prevent adding a plant twice in a row
    if (selectedPlant !== null && selectedPlant.name === plantName) {
        return;
    }
    const newPlant = {
      name: plantName,
      type: selectedPlant,
      soilSensor: soilSensor,
      autoWatering: autoWatering,
      ...(autoWatering ? { moistureValue } : { wateringInterval }),
    };
    onAddPlant(newPlant);
  };

  const handleSelectPlant = (index) => {
    setSelectedPlantIndex(index);
    setSelectedPlant(plants[index]);
  };
  var backButtonSrc = process.env.PUBLIC_URL+"/img/Back Arrow.svg";
  var nextSrc = process.env.PUBLIC_URL+"/img/Next.svg";
  var prevSrc = process.env.PUBLIC_URL+"/img/Previous.svg";

  return (
    <div className="add-plant">
      <div className="plant-details-header">
        <img src={backButtonSrc} className="back-button" onClick={onBack}/>
        <h1>Add A Plant!</h1>
      </div>
      <Carousel
        showStatus={false}
        showThumbs={false}
        showArrows={true}
        showIndicators={false}
        selectedItem={selectedPlantIndex}
        onChange={handleSelectPlant}
        swipeable={true}
        dynamicHeight={false}
        width='300px'
        renderArrowPrev={(clickHandler, hasPrev) => {
          return (
            <div class='arrow left-arrow' onClick={clickHandler}>
            <img src={prevSrc}/>
            </div>
          );
        }}
        renderArrowNext={(clickHandler, hasNext) => {
          return (
            <div class='arrow right-arrow' onClick={clickHandler}>
            <img src={nextSrc}/>
            </div>
          );
        }}>
        {plants.map((plant, index) => (
            <div key={plant} className={`plant-item${selectedPlantIndex === index ? ' selected' : ''}`}>
            <img src={process.env.PUBLIC_URL + "/img/"+plant+".svg"} alt={plant} />
            <p>{plant}</p>
            </div>
        ))}
      </Carousel>

      <div className="plant-details">
        <input
          type="text"
          placeholder="Enter plant name"
          value={plantName}
          onChange={(e) => setPlantName(e.target.value)}
        />

        <div className="options">
          <label>
            <input
              type="checkbox"
              checked={soilSensor}
              onChange={(e) => setSoilSensor(e.target.checked)}
            />
            Soil Sensor
          </label>
          <label>
            <input
              type="checkbox"
              checked={autoWatering}
              onChange={(e) => setAutomatedWatering(e.target.checked)}
            />
            Automated Watering
          </label>
        </div>

        {autoWatering ? (
          <input
            type="text"
            placeholder="Enter moisture value"
            value={moistureValue}
            onChange={(e) => setMoistureValue(e.target.value)}
          />
        ) : (
          <input
            type="text"
            placeholder="Enter days between watering"
            value={wateringInterval}
            onChange={(e) => setDaysBetweenWatering(e.target.value)}
          />
        )}

        <button className="add-to-shelf" onClick={handleAddPlant}>
          Add To Shelf!
        </button>
      </div>
    </div>
  );
};

export default AddPlant;