import { Switch } from '@mui/material';
import React, { useState }  from 'react';
import './PlantDetails.css';

const PlantDetails = ({ plant, onBack, onRemovePlant, onUpdatePlantDetails, onWaterPlantNow }) => {
  const [soilSensorEnabled, setSoilSensorEnabled] = useState(false);
  const [autoWateringEnabled, setAutoWateringEnabled] = useState(false);
  const [daysBetweenWatering, setDaysBetweenWatering] = useState('');
  const [soilMoistureLevel, setSoilMoistureLevel] = useState('');

  const handleSoilSensorToggle = () => {
    setSoilSensorEnabled(!soilSensorEnabled);
  };

  const handleAutoWateringToggle = () => {
    setAutoWateringEnabled(!autoWateringEnabled);
  };

  var wateringLog = plant.wateringLog.slice(-3).reverse();
  for (var i = wateringLog.length; i < 3; i++) {
    wateringLog.push("...");
  }
  var backButtonSrc = process.env.PUBLIC_URL+"/img/Back Arrow.svg";
  return (
    <div className="plant-details">
    <link rel="stylesheet" type="text/css" href="//fonts.googleapis.com/css?family=Amatic%20SC" />

      <div className="plant-details-header">
        <img src={backButtonSrc} className="back-button" onClick={onBack}/>
        <h1>{plant.name}</h1>
      </div>
      <img src={plant.image} alt={plant.name} />
      <div className="plant-details-info">
        <div className="plant-info-with-header">
            <h3>{plant.type}</h3>
            <div className="plant-info">
            <p>Date added: {plant.dateAdded}</p>
            <p>Last watered: {plant.lastWatered}</p>
            <p>Soil moisture level: {plant.soilMoisture}</p>
            </div>
        </div>
        <div className="watering-log">
          <h3>Watering Log</h3>
          <ul>
            {wateringLog.map((date, index) => (
              <li key={index}>{date}</li>
            ))}
          </ul>
        </div>
      </div>
      <div className="plant-details-info">
        <div className='left-switch'>
            <label>{soilSensorEnabled ? 'Soil Sensor Enabled' : 'No Soil Sensor'}</label><br/>
            <Switch checked={soilSensorEnabled} onChange={handleSoilSensorToggle} color="success"/>
        </div>
        <div className='right-switch'>
            <label>{autoWateringEnabled ? 'Automated Watering' : 'Manual Watering'}</label><br/>
            <Switch checked={autoWateringEnabled} onChange={handleAutoWateringToggle} color="success"/>
        </div>
      </div>

      {autoWateringEnabled && soilSensorEnabled ? (
        <div>
            <label>Soil Moisture Watering Threshold</label><br/>
            <input
            type="text"
            placeholder={plant.soilMoisture}
            value={plant.soilMoisture}
            onChange={(e) => setSoilMoistureLevel(e.target.value)}
            />
        </div>
      ) : (
        <div>
            <label>Preferred Days Between Watering</label><br/>
            <input
            type="text"
            placeholder={plant.wateringInterval}
            value={plant.wateringInterval}
            onChange={(e) => setDaysBetweenWatering(e.target.value)}
            />
        </div>
      )}

      <button className="update-plant-details-button" onClick={() => onUpdatePlantDetails(soilSensorEnabled, autoWateringEnabled, daysBetweenWatering, soilMoistureLevel)}>
        Update Plant Details
      </button>

      <button className="water-plant-now-button" onClick={onWaterPlantNow}>
        Water Plant Now
      </button>

      <button className="remove-plant-button" onClick={onRemovePlant}>
        Remove Plant from Shelf
      </button>
    </div>
  );
};

export default PlantDetails;