import React, { useState, useEffect } from 'react';
import { firestore, auth, provider } from './firebase';
import { getAuth, signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import { collection, getDocs, getDoc, addDoc, doc } from "firebase/firestore"; 
import './App.css';
import PlantDetails from './PlantDetails/PlantDetails';
import AddPlant from './AddPlant';
import Login from './Login/Login';


const PlantInfo = ({ name, lastWatered, onClick }) => {
  return (
    <div className="plant" onClick={() => onClick(name)}>
      <h3>{name}</h3>
      <p>Last watered: {lastWatered.toLocaleString()}</p>
    </div>
  );
};

const PlantSVG = ({name}) => {
  var src = process.env.PUBLIC_URL+"/img/"+name+".svg";
  return (<img src={src}></img>);
}

const App = () => {
  const [plants, setPlants] = useState([]);
  const [selectedPlant, setSelectedPlant] = useState(null);
  const [showAddPlant, setShowAddPlant] = useState(false);
  const [currentPage, setCurrentPage] = useState('main');
  const [isSignedIn, setIsSignedIn] = useState(false);


  const handlePlantClick = (plant) => {
    setSelectedPlant(plant);
    setCurrentPage('plantDetails');
  };

  const handleAddPlantClick = () => {
    setCurrentPage('addPlant');
  };
  
  const handleBackClick = () => {
    setSelectedPlant(null);
    setCurrentPage('main');
  };

  const dateToDateString = (date) => {
    return date.toLocaleDateString('en-US', { month: '2-digit', day: '2-digit', year: 'numeric' }).replace(/\//g, '-');
  }

  const handleAddPlant = (plant) => {
    // connect to firebase and add document to the plants collection with the plant info
    // then update the plants state variable
    setShowAddPlant(false);
    var currentDate = new Date();
    var currentDateString = dateToDateString(currentDate);

    const newPlant = {
      name: plant.name,
      type: plant.type,
      description: "A beautiful " + plant.name + " plant.",
      lastWatered: currentDateString,
      wateringLog: [currentDateString],
      moistureLevel: "Medium",
      soilSensor: false,
      autoWater: false,
      dateAdded: currentDateString,
    };
    setPlants([...plants, newPlant]);

    // Add new plant to firebase
    const addPlant = async () => {
      try {
        const docRef = await addDoc(collection(firestore, "plants"), newPlant);
        console.log("Document written with ID: ", docRef.id);
      } catch (e) {
        console.error("Error adding document: ", e);
      }
    }
    addPlant();
    setCurrentPage('main');
  }
  
  const waterPlantNow = async (plantId) => {
    const plantRef = await getDoc(doc(firestore, "plants", plantId));
    var currentDate = new Date();
    var currentDateString = dateToDateString(currentDate);
    plantRef.update({ lastWatered: currentDateString, wateringLog: [...plantRef.wateringLog, currentDateString]});
  };

  const removePlant = async (plantId) => {
    const plantRef = await getDoc(doc(firestore, "plants", plantId));
    plantRef.delete();
  };

  const updatePlantDetails = async (plantId, soilSensorEnabled, autoWateringEnabled, daysBetweenWatering, soilMoistureLevel) => {
    const plantRef = await getDoc(doc(firestore, "plants", plantId));
    plantRef.update({
      soilSensorEnabled: soilSensorEnabled,
      autoWateringEnabled: autoWateringEnabled,
      daysBetweenWatering: daysBetweenWatering,
      soilMoistureLevel: soilMoistureLevel,
    });
  };

  useEffect(() => {
    const fetchPlants = async () => {
      try {
        const snapshot = await getDocs(collection(firestore, 'plants'));
        const plants = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setPlants(plants);
      } catch (error) {
        console.error('Error fetching plants:', error);
      }
    };
  
    fetchPlants();
  }, []);

  var listOfPlants = [
    "Aloe",
    "Baby Toes",
    "Bamboo",
    "Black Dragon Snake Plant",
    "Cactus",
    "Cast Iron Plant",
    "Chinese Money Plant",
    "Fingers Snake Plant",
    "Futura Robusta Snake Plant",
    "Leafy",
    "Monstera",
    "Pothos Plant",
    "Sago Palm",
    "Snake Plant",
    "Spider Plant",
    "String of Pearls",
    "Succulent"
  ];

  var listOfIndicators = [
    "Automated Water Symbol",
    "Back Arrow",
    "Green Status",
    "Key",
    "Next",
    "Previous",
    "Red Status",
    "Water Indicator Urgent",
    "Water Indicator",
    "Yellow Status"
  ];
  var addPlantImage = process.env.PUBLIC_URL+"/img/Add a Plant Button.svg";
  return (
    <div>
      <link rel="stylesheet" type="text/css" href="//fonts.googleapis.com/css?family=Amatic%20SC" />

      { currentPage === 'plantDetails' &&
        <PlantDetails
          plant={{
            name: selectedPlant.name,
            type: selectedPlant.type,
            image: process.env.PUBLIC_URL + "/img/" + selectedPlant.type + ".svg",
            dateAdded: selectedPlant.dateAdded,
            lastWatered: selectedPlant.lastWatered,
            soilMoisture: selectedPlant.soilMoisture,
            wateringInterval: selectedPlant.wateringInterval,
            wateringLog: selectedPlant.wateringLog,
            description: selectedPlant.description,
          }}
          onBack={handleBackClick}
          onRemovePlant={() => removePlant(selectedPlant.id)}
          onUpdatePlantDetails={(soilSensorEnabled, autoWateringEnabled, daysBetweenWatering, soilMoistureLevel) => updatePlantDetails(selectedPlant.id, soilSensorEnabled, autoWateringEnabled, daysBetweenWatering, soilMoistureLevel)}
          onWaterPlantNow={() => waterPlantNow(selectedPlant.id)}
        />
        }
        { currentPage === 'main' && (
        <div className="App">
          <h1>Plant Watering Dashboard</h1>
          <Login/>
          <div className="plant-shelf">
            {plants.map((plant) => (
              <div>
                <PlantSVG name={plant.type} />
                <PlantInfo name={plant.name} lastWatered={plant.lastWatered.toLocaleString()} 
                onClick={() => handlePlantClick(plant)} />
              </div>
            ))}
          </div>
          <div className="bottom-buttons">
            <img src={addPlantImage} onClick={handleAddPlantClick}></img>
          </div>
        </div>
      )}
      {currentPage === 'addPlant' && <AddPlant plants={listOfPlants} onBack={handleBackClick} onAddPlant={handleAddPlant} />}
    </div>
  );
};

export default App;